import React, { useState, useEffect } from 'react';
import MarketChart from './MarketChart';
import { FiArrowLeft } from 'react-icons/fi'; // Import back arrow icon

const TopMoversCard = ({ title, fetchData, type }) => {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [selectedStock, setSelectedStock] = useState(null); // e.g., "AAPL"
    const [selectedStockName, setSelectedStockName] = useState(null); // e.g., "Apple Inc."

    useEffect(() => {
        const loadData = async () => {
            try {
                setLoading(true);
                setError(null);
                const response = await fetchData();
                setData(response.data || []);
            } catch (err) {
                setError('Data could not be loaded.');
                console.error("Failed to fetch top movers:", err);
            } finally {
                setLoading(false);
            }
        };
        loadData();
    }, [fetchData]);

    const getRowColor = (stock) => {
        const change = parseFloat(stock.changeAmount);
        if (type === 'gainer' || (type === 'active' && change >= 0)) return 'text-green-600';
        if (type === 'loser' || (type === 'active' && change < 0)) return 'text-red-600';
        return 'text-gray-800';
    };

    const handleStockSelect = (stock) => {
        setSelectedStock(stock.ticker);
        setSelectedStockName(stock.name); // Store the name for the chart title
    };

    const renderContent = () => {
        if (loading) {
            return <p className="p-4 text-sm text-gray-500 text-center">Loading data...</p>;
        }
        if (error) {
            return <p className="p-4 text-sm text-red-600 break-words text-center">{error}</p>;
        }

        // --- CHART VIEW ---
        // If a stock is selected, show the chart and a back button
        if (selectedStock) {
            return (
                <div className="p-2">
                    <button
                        onClick={() => setSelectedStock(null)}
                        className="flex items-center text-sm text-sky-600 font-semibold hover:text-sky-700 p-1 rounded-md transition-colors"
                    >
                        <FiArrowLeft className="w-4 h-4 mr-1" />
                        Back to list
                    </button>
                    <div className="h-[250px] w-full mt-2">
                        <MarketChart ticker={selectedStock} chartTitle={selectedStockName} />
                    </div>
                </div>
            );
        }

        // --- LIST VIEW (Default) ---
        return (
            <div className="px-1 py-2">
                <table className="w-full">
                    <thead>
                        <tr className="text-left text-gray-500 uppercase font-medium text-[11px]">
                            <th className="py-1.5 px-1.5 font-semibold">Symbol</th>
                            <th className="py-1.5 px-1.5 font-semibold text-right whitespace-nowrap">Price</th>
                            <th className="py-1.5 px-1.5 font-semibold text-right whitespace-nowrap">Chg</th>
                            <th className="py-1.5 px-1.5 font-semibold text-right whitespace-nowrap">%Chg</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-100">
                        {data.slice(0, 5).map((stock) => (
                            <tr
                                key={stock.ticker}
                                title={`View chart for ${stock.name}`}
                                className="text-xs cursor-pointer hover:bg-sky-50 transition-colors duration-150"
                                onClick={() => handleStockSelect(stock)}
                            >
                                <td className="py-2 px-1.5 font-bold text-gray-800 truncate">{stock.ticker}</td>
                                <td className="py-2 px-1.5 text-right font-medium text-gray-700 whitespace-nowrap">
                                    ${parseFloat(stock.price).toFixed(2)}
                                </td>
                                <td className={`py-2 px-1.5 text-right font-bold whitespace-nowrap ${getRowColor(stock)}`}>
                                    {parseFloat(stock.changeAmount) >= 0 ? '+' : ''}{parseFloat(stock.changeAmount).toFixed(2)}
                                </td>
                                <td className={`py-2 px-1.5 text-right font-bold whitespace-nowrap ${getRowColor(stock)}`}>
                                    {stock.changePercentage}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        );
    };

    return (
        <div className="bg-white">
            {/* The title is now part of the tab button in the parent MarketMovers component,
                so we don't need to render it here. This keeps the card focused on content.
            */}
            {/* <h4 className="font-bold text-sm p-2 border-b border-gray-200/90 text-gray-800">{title}</h4> */}

            {renderContent()}
        </div>
    );
};

export default TopMoversCard;